.container {
  position: relative;
  max-width: 1060px;
  margin: 0 auto;
  padding: 0 100px;
  background-color: transparent; }
  .container--flex {
    display: flex;
    flex-direction: column; }
  @media (max-width: 959px) {
    .container {
      padding-left: 40px;
      padding-right: 40px;
      margin: 0 auto; } }
  @media (max-width: 767px) and (min-width: 320px) {
    .container {
      padding-right: 20px;
      padding-left: 20px; } }

.container-1440 {
