  margin: 0 auto; }

.visually-hidden:not(:focus):not(:active),
input[type="checkbox"].visually-hidden,
input[type="file"].visually-hidden,
input[type="radio"].visually-hidden {
  position: absolute;
  overflow: hidden;
  clip: rect(0 0 0 0);
  width: 1px;
  height: 1px;
  margin: -1px;
  padding: 0;
  white-space: nowrap;
  border: 0;
  clip-path: inset(100%); }

picture {
  display: block; }

.rub {
  font-family: "Lato", "Arial", sans-serif;
  font-size: inherit;
  font-weight: inherit; }

.buffer {
  position: absolute;
  top: -1000px;
  left: -1000px;
  visibility: hidden;
  white-space: nowrap; }

.btn {
  display: inline-block;
  vertical-align: top;
  padding: 0;
  text-align: center;
  font-family: inherit;
  background-color: rgba(255, 255, 255, 0);
  border: 0;
  border-radius: 0;
  transition: opacity 0.3s ease;
  display: inline-block;
  vertical-align: top;
  padding: 14px 39px;
  color: #ffffff;
  font-weight: 500;
  font-size: 24px;
  line-height: 29px;
  border: 0;
  word-break: break-all;
  letter-spacing: 0;
  white-space: normal;
  text-transform: uppercase;
  transition: transform 0.2s linear; }
  .btn--intro {
    background-color: #006dfe;
    flex-shrink: 0; }
  .btn:hover {
    transition: transform 0.2s linear;
    transform: scale(1.1); }
  .btn:active {
    box-shadow: none; }

.header {
  position: fixed;
  top: 0;
  left: 0;
  width: 100%;
  z-index: 1;
  padding-bottom: 20px;
  transition: background-color 0.2s linear; }
  .header--active {
    transition: background-color 0.2s linear;
    background-color: rgba(0, 0, 0, 0.8); }

.header__wrapper {
  display: flex;
  flex-wrap: wrap;
  align-items: center;
  justify-content: space-between;
  padding-top: 56px; }

.header__list {
  margin: 0;
  padding: 0;
  list-style: none;
  display: flex;
  flex-wrap: wrap; }

@media (max-width: 767px) and (min-width: 320px) {
  .header__logo {
    width: 100%;
    margin: 10px 40px 40px 0; } }

.header__logo .header__logo-link {
  flex-shrink: 0;
  display: block; }
  .header__logo .header__logo-link .header__logo-pic {
    transition: fill 0.2s linear, transform 0.2s linear;
    fill: white; }
    .header__logo .header__logo-link .header__logo-pic:hover {
      transform: scale(1.1);
      transition: fill 0.2s linear, transform 0.2s linear;
      fill: #006dfe; }
    @media (max-width: 767px) and (min-width: 320px) {
      .header__logo .header__logo-link .header__logo-pic {
        width: 30%;
        height: auto; } }

.header__item {
  margin-right: 82px; }
  @media (max-width: 767px) and (min-width: 320px) {
    .header__item {
      flex-grow: 1;
      width: 30%;
      margin-bottom: 14px; }
      .header__item:nth-child(2n) {
        margin-right: 0; } }
  .header__item:last-child {
    margin-right: 0; }

.header__link {
  font-size: 18px;
  line-height: 24px;
  padding: 10px;
  color: white;
  text-decoration: none;
  transition: background-color 0.2s linear; }
  .header__link:hover, .header__link:focus {
    transition: background-color 0.2s linear;
    background-color: #006dfe; }
  .header__link--active, .header__link:active {
    transition: background-color 0.2s linear;
    background-color: #006dfe; }

